.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
.centered {
  position: absolute;
  top: 30%;
  left: 30%;
  transform: translate(-50%, -50%);
  color:white;
  font-size:50px;
  font-style:italic;
}
.centered1 {
  position: absolute;
  top: 40%;
  left: 30%;
  transform: translate(-50%, -50%);
  color:white;
  font-size:100px;
  font-family:Arial, Helvetica, sans-serif;
}
.aboutme {
  position:relative;
  top:30%;
  left:50%;
  transform: translate(-50%, 150%);
  text-align:center;
  color:black;
  font-size:50px;
  font-family: Arial, Helvetica, sans-serif;
  font-style:oblique;
}
.aboutme:before, .aboutme:after {
  position: absolute;
  top: 51%;
  overflow: hidden;
  width: 45.35%;
  height: 1px;
  content: '\a0';
  background-color: black;
}
.aboutme:before {
  margin-left: -45.1%;
  text-align: right;
}
.bio {
  display:flex;
  text-align:left;
  padding-left:10%;
  padding-top:10%;
  font-size:25px;
  font-family:'Times New Roman', Times, serif;
  

}


@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
